function formatDate(date) {
    var d = new Date(date),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear(),
        hours = d.getUTCHours(),
        minutes = d.getUTCMinutes();

    if (month.length < 2)
        month = '0' + month;
    if (day.length < 2)
        day = '0' + day;

    return [day, '/', month, '/', year, ' ', ' ', hours, ':', minutes].join('');
}

const form = document.getElementById('search_form_news');

form.addEventListener('submit', function(e) {

    e.preventDefault();

    const token = document.querySelector('meta[name="csrf-token"]').content,
        url = this.getAttribute('action'),
        q = document.getElementById('search_input').value;

    fetch(url, {
        headers: {
            'Content-Type': 'application/json',
            'X-CSRF-TOKEN': token
        },
        method: 'post',
        body: JSON.stringify({
            q: q
        })
    }).then(res => {
        res.json().then(data => {

            const posts = document.getElementById('search_res'),
                search_op = document.getElementById('search_op'),
                loading = document.getElementById('load');

            search_op.style.display = 'none';
            loading.style.display = 'block';

            loading.innerHTML = `<div class="spinner-grow text-info" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>`;

            setTimeout(() => {

                posts.innerHTML = '';

                data.news.data.forEach(e => {
                    posts.innerHTML += `
                    `
                });

            }, 2500);

            setTimeout(() => {

                loading.innerHTML = '';
                loading.style.display = 'none';

            }, 2600);

        })
    }).catch(error => {

    })

})